<template>
  <div class="text_list">
    <div class="list_content" v-for="item in namelist" :key="item.id"  @click="clickGo(item.file_id_str)">
      <span class="text_time">{{ item.fabu_time.substring(5, 10) }}</span>
      <span class="text_content" >{{ item.title }}</span>
    </div>
  </div>
</template>

<script>
import { computed, onMounted, reactive, toRefs, ref, onBeforeMount } from "vue";
import { useRoute } from "vue-router";
import { useStore } from "vuex";

export default {
  name: "Detail",
  setup() {
    const route = useRoute();
    const store = useStore();
    const topTitleName = ref("");
    const shuju = reactive({
      namelist: [],
    });
    const topTitle = computed(() => {
      return store.state.home.toptitle;
    });

    onMounted(() => {
      let info = JSON.parse(
        window.localStorage.getItem(route.query.name) || []
      );
      if (info == "") {
        console.log("无数据");
      } else {
        shuju.namelist = info;

      }
    });
    
    const clickGo = (title) => {
        window.location.href = title
    }
   

    return {
      topTitle,
      topTitleName,
      ...toRefs(shuju),
      clickGo
    };
  },
};
</script>

<style lang="scss" scoped>
.text_list {
  background-color: #fff;
  border-radius: 5px;

  display: flex;
  flex-wrap: wrap;
  padding: 6px;

  .list_content {
    display: flex;
    width: 95%;
    align-items: center;
    padding: 10px;
    border-bottom: 1px solid rgb(233, 233, 233);

    .text_time {
      flex: 1;
      display: block;
      line-height: 30px;
      background-color: #543c92;
      width: 55px;
      height: 30px;
      color: #fff;
      border-radius: 5px;
      margin-right: 15px;
      font-weight: 700;
    }
    .text_content {
      flex: 4;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
      padding-right: 10px;
      text-align: left;
    }
  }
  .list_content:last-child {
    border-bottom: none;
  }
}
</style>
>
